h1 { color: red; }

* { margin: 0; padding: 0; }

body { font-family: sans-serif; }

a { text-decoration: none; }

.top-heder { width: 100%; height: 47px; background: url("../svg/Svg/Bg_Dark2.svg") no-repeat; background-color: #013056; }

.login-img, .registr-img { width: 30px; position: relative; top: 3px; }

.phone { color: #005c7a; }

.phone { color: #caddea; }

.phone span { font-weight: 800; }

.login-top, .registr { color: #ffffff; }

.top-heder { display: flex; justify-content: center; }

.top-conteiner { margin: 0; width: 100%; display: flex; align-items: center; }

.phone-conteiner { width: 100%; }

.top-nav_menu { list-style-type: none; display: flex; height: 100%; margin: 0; }

.top-nav_menu li { color: #ffffff; border-right: 2px solid #ffffff; padding-right: 50px; font-size: 20px; height: 100%; padding-left: 40px; }

.top-nav_menu li:hover { background-color: #caddea; color: #075da2; cursor: pointer; }

.nav-heder { background-color: #1b7fd0; height: 35px; display: flex; align-items: center; box-shadow: 2px 4px 7px #0983e5; }

.heder-post { background: url("../svg/Layer_11.png") no-repeat, url("../svg/Layer_12.png") 100% 0 no-repeat; height: 600px; }

.heder-post .logo-site { width: 597px; height: 82px; padding-top: 25px; }

.heder-post .heders-h1 h1 { padding-top: 70px; padding-bottom: 40px; text-transform: uppercase; color: #0983e5; }

.heder-post .heder-list h3 { text-transform: uppercase; padding: 8px; font-size: 22px; }

.heder-post .heder-list ul { list-style: none; }

.heder-post .heder-list ul li { color: #0983e5; position: relative; bottom: 3px; padding-top: 13px; }

.heder-post .heder-list ul li::before { content: ""; display: inline-block; height: 10px; width: 10px; vertical-align: middle; border-radius: 50%; background-color: #0983e5; margin-right: 9px; margin-bottom: 5px; }

.heder-post .heder-list ul li span { font-size: 18px; color: black; }

.input-heder { margin-top: 40px; }

.input-heder .point { width: 230px; height: 35px; border: 1px solid #0983e5; display: flex; justify-content: center; align-items: center; margin: 15px; color: #0983e5; }

.foter-block { background-color: #caddea; height: 80px; display: flex; align-items: center; }

.futer-top { height: 45px; }

.futer-top .foter-payment ul { list-style-type: none; display: flex; }

.futer-top .foter-payment ul li { margin-right: 20px; }

.futer-top .foter-payment ul li img { height: 45px; width: 70px; }

.soc { display: flex; }

.social { display: flex; justify-content: center; align-items: center; }

.social ul { list-style-type: none; display: flex; height: 30px; }

.social ul li { margin-right: 35px; }

.social ul li img { height: 100%; width: 100%; }

.futer-bootom { background: url("../svg/Svg/Bg_Dark2.svg") no-repeat; background-color: #013056; height: 375px; }

.futer-bootom .list-bootom { padding-top: 32px; padding-right: 36px; }

.futer-bootom .futer-list ul li { list-style-type: none; padding-top: 10px; }

.futer-bootom .futer-list ul li a { color: #ffffff; }
